import { useSectionInView, fadeIn, motion } from "../hooks/useSectionInView";

const PartA = () => {
  const { ref: titleRef, animation: titleAnimation } = useSectionInView();
  const { ref: overviewRef, animation: overviewAnimation } = useSectionInView();
  const { ref: campaignRef, animation: campaignAnimation } = useSectionInView();

  const campaigns = [
    { title: "1. Digital Wellness Hub", desc: "A website and mobile app providing guided meditation, stress-relief techniques, and a directory of Dubais wellness spots." },
    { title: "2. Influencer Wellness Retreat", desc: "Partnering with global influencers for a luxury wellness retreat in Dubai to showcase the citys health-focused experiences." },
    { title: "3. Mindful Tourism Packages", desc: "Exclusive travel packages featuring wellness hotels, yoga retreats, and spa experiences designed for holistic rejuvenation." },
    { title: "4. Corporate Wellness Initiative", desc: "A program that encourages Dubai businesses to adopt wellness-friendly policies with office meditation zones and fitness breaks." },
    { title: "5. Eco-Wellness Festivals", desc: "Seasonal festivals promoting sustainability, healthy living, and mindfulness through eco-friendly activities and organic food markets." },
    { title: "6. Wellness & Tech Expo", desc: "A global summit in Dubai showcasing the latest advancements in health tech, AI-driven fitness, and biohacking solutions." }
  ];

  return (
    <div className="relative w-full min-h-screen flex flex-col items-center justify-center bg-gradient-to-b from-[#FAF9F6] to-[#ECE9E6] text-gray-900 px-8 md:px-20 lg:px-40 py-24">
      
      {/* Title Section */}
      <motion.div ref={titleRef} initial="hidden" animate={titleAnimation} variants={fadeIn} className="mb-12 text-center">
        <h1 className="text-5xl md:text-6xl font-extrabold tracking-wide uppercase text-[#2D2D2D] font-inter">
          Part A: One-Year Marketing Campaign
        </h1>
        <p className="text-lg md:text-xl text-gray-700 font-worksans mt-4">
          <span className="italic">"Dubai Wellness Oasis"</span>
        </p>
        <div className="w-20 h-1 bg-[#AA8453] mx-auto mt-4 rounded-full"></div>
      </motion.div>

      {/* Overview Section */}
      <motion.div ref={overviewRef} initial="hidden" animate={overviewAnimation} variants={fadeIn} className="max-w-3xl text-center mb-16">
        <p className="text-lg md:text-xl text-gray-700 leading-relaxed font-worksans">
          The <strong>Dubai Wellness Oasis</strong> campaign is a one-year initiative designed to position Dubai as the world's leading wellness destination. 
          This campaign introduces <strong>six strategic ideas</strong> to elevate wellness tourism, sustainable health experiences, and holistic living in Dubai.
        </p>
      </motion.div>

      {/* Campaign Ideas Section */}
      <motion.div ref={campaignRef} initial="hidden" animate={campaignAnimation} variants={fadeIn} className="max-w-5xl grid grid-cols-1 md:grid-cols-2 gap-12">
        {campaigns.map(({ title, desc }, index) => (
          <motion.div
            key={index}
            initial="hidden"
            animate="visible"
            variants={fadeIn}
            transition={{ delay: index * 0.2 }} // Cascading fade-in effect
            className="flex flex-col items-center md:items-start bg-white shadow-lg rounded-xl px-8 py-6"
          >
            <h2 className="text-xl md:text-2xl font-semibold text-gray-900 font-inter mb-4">
              {title}
            </h2>
            <p className="text-lg text-gray-600 leading-relaxed font-worksans">
              {desc}
            </p>
          </motion.div>
        ))}
      </motion.div>

    </div>
  );
};

export default PartA;
