import { useSectionInView, fadeIn, motion } from "../hooks/useSectionInView";

const PartB = () => {
  const { ref: titleRef, animation: titleAnimation } = useSectionInView();
  const { ref: overviewRef, animation: overviewAnimation } = useSectionInView();
  const { ref: featuresRef, animation: featuresAnimation } = useSectionInView();

  const features = [
    { title: "1. Modular & Expandable Design", desc: "Customizable tiny homes with modular layouts that allow for expansion based on need and budget." },
    { title: "2. Smart Technology Integration", desc: "Automated energy-efficient systems for lighting, temperature control, and security via mobile app." },
    { title: "3. Sustainable & Eco-Friendly Materials", desc: "Constructed using recycled materials, solar panels, and water-saving solutions to reduce environmental impact." },
    { title: "4. Community-Driven Living Spaces", desc: "Tiny home villages designed to foster a sense of community with shared green spaces, wellness centers, and social hubs." },
    { title: "5. Affordable & Flexible Ownership", desc: "Low-cost financing, rent-to-own options, and flexible leasing plans to make homeownership accessible." }
  ];

  return (
    <div className="relative w-full min-h-screen flex flex-col items-center justify-center bg-gradient-to-b from-[#FAF9F6] to-[#ECE9E6] text-gray-900 px-8 md:px-20 lg:px-40 py-24">
      
      {/* Title Section */}
      <motion.div ref={titleRef} initial="hidden" animate={titleAnimation} variants={fadeIn} className="mb-12 text-center">
        <h1 className="text-5xl md:text-6xl font-extrabold tracking-wide uppercase text-[#2D2D2D] font-inter">
          Part B: Affordable Housing Solution
        </h1>
        <p className="text-lg md:text-xl text-gray-700 font-worksans mt-4 italic">
          "Smart Tiny Living"
        </p>
        <div className="w-20 h-1 bg-[#AA8453] mx-auto mt-4 rounded-full"></div>
      </motion.div>

      {/* Overview Section */}
      <motion.div ref={overviewRef} initial="hidden" animate={overviewAnimation} variants={fadeIn} className="max-w-3xl text-center mb-16">
        <p className="text-lg md:text-xl text-gray-700 leading-relaxed font-worksans">
          The <strong>Smart Tiny Living</strong> initiative is designed to address the growing demand for affordable housing while promoting <strong>sustainability, smart technology, and community-driven lifestyles</strong>. 
          This concept focuses on <strong>five key features</strong> to redefine modern living.
        </p>
      </motion.div>

      {/* Key Features Section */}
      <motion.div ref={featuresRef} initial="hidden" animate={featuresAnimation} variants={fadeIn} className="max-w-5xl grid grid-cols-1 md:grid-cols-2 gap-12">
        {features.map(({ title, desc }, index) => (
          <motion.div
            key={index}
            initial="hidden"
            animate="visible"
            variants={fadeIn}
            transition={{ delay: index * 0.2 }} // Staggered fade-in effect
            className="flex flex-col items-center md:items-start bg-white shadow-lg rounded-xl px-8 py-6"
          >
            <h2 className="text-xl md:text-2xl font-semibold text-gray-900 font-inter mb-4">
              {title}
            </h2>
            <p className="text-lg text-gray-600 leading-relaxed font-worksans">
              {desc}
            </p>
          </motion.div>
        ))}
      </motion.div>

    </div>
  );
};

export default PartB;
